  -o-transition: width 500ms,height 500ms;
  transition: width 500ms,height 500ms;
}
.mainNavigation > .navImage{
    -webkit-transform:translate(6em,6em);
        -ms-transform:translate(6em,6em);
            transform:translate(6em,6em);
    position: absolute;
  }
.mainNavigation > .navImage > img {
      position: absolute;
      z-index: 100;
      width:6em;
      height:6em;
      border-radius:50%;
      -webkit-transform:translate(-0.5em,-0.5em);
          -ms-transform:translate(-0.5em,-0.5em);
              transform:translate(-0.5em,-0.5em);
    }
